@charset "UTF-8";

@import 'partials/_mixin';
@import 'partials/_variables';

:root {
	--vh: 100%;
}

html, 
body {
	position: relative;
	width: 100%;
	background-color: var(--color-base-070);
	@include overflow-scroll(default, dark, large);
}

body.hidden {
	overflow: hidden;

	.page-board {
		overflow: hidden;
	}
}

.skip {
	position: fixed;
	top: -50px;
	left: 0;
	z-index: 10000;
	width: 100%;
	height: 50px;
	background-color: var(--color-base-000);

	a {
		display: flex;
		justify-content: center;
		align-items: center;
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		background-color: var(--color-base-000);
		color: var(--color-base-100);
		text-decoration: underline;
		text-align: center;

		&:not(:focus) {
			top: -50px
		}
	}

	&:focus-within {
		top: 0;
	}
}

.header {
	// flex: 1 1 100%;
	height: 98px;
	margin-bottom: 12px;
	// background: #fff;
	z-index: 2;
	transition: all .2s;
	// box-shadow: 0px 7.2px 21.6px rgba(0, 0, 0, 0.05);
	// border-bottom: 1px solid #dedfe1;

	.header-inner {
		padding: 0 32px;
		display: flex;
		justify-content: space-between;
		align-items: center;
		height: 100%;

		.header-title {
			display: flex;
			align-items: center;
			column-gap: 12px;

			.header-name {
				font-weight: 700;
				font-size: 24px;
			}

			.header-noti {
				padding: 6px 8px;
				border-radius: 4px;
				background-color: var(--color-base-080);
				box-sizing: border-box;

				.header-noti-bx {
					display: flex;
					column-gap: 8px;
					line-height: 20px;
					font-size: 14px;
					color: var(--color-base-020);

					.noti-tx{
						display: flex;
						align-items: center;
						column-gap: 2px;

						&::before {
							content: '';
							display: block;
							width: 20px;
							height: 20px;
							background-color: var(--color-base-020);
							mask-image: icon(time);
							mask-repeat: no-repeat;
							mask-size: cover;
							mask-position: center;
						}
					}

					.noti-tx-info {
						display: flex;
						align-items: center;
						column-gap: 8px;

						&::before {
							content: '';
							display: block;
							width: 3px;
							height: 3px;
							border-radius: 50%;
							background-color: var(--color-base-050);
						}
					}
				}
			}
		}

		.btn-menu {
			display: flex;
			width: 20px;
			height: 20px;
			cursor: pointer;
			background-color: var(--color-base-010);
			background-color: rgb(224, 224, 224);

			.btn-text {
				@include hidden;
			}
		}
		
		.header-util {
			display: flex;

			.btn-list {
				display: flex;
				column-gap: 20px;
				align-items: center;

				.btn-item {
					position: relative;

					.btn-h {
						// position: relative;
						display: flex;
						width: 32px;
						height: 32px;
						cursor: pointer;
						border-radius: 10px;

						.btn-text {
							@include hidden;
						}

						&::before {
							content: '';
							position: absolute;
							top: 0;
							left: 0;
							display: block;
							width: 32px;
							height: 32px;
							mask-repeat: no-repeat;
							mask-position: center center;
							mask-size: cover;
						}

						&.search {
							position: relative;

							&::before {
								background-color: var(--color-base-010);
								mask-image: icon(usersearch);
							}
						}

						&.mypage {
							position: relative;

							&::before {
								content: '';
								position: absolute;
								top: 0;
								left: 0;
								display: block;
								width: 32px;
								height: 32px;
								background-color: var(--color-base-040);
								mask-image: icon(my-page);
								mask-repeat: no-repeat;
								mask-position: center center;
								border-radius: 50%;
							}
						}

						&.fullmenu {
							position: relative;
							width: 36px;
							height: 36px;

							&::before {
								content: '';
								position: absolute;
								top: 0;
								left: 0;
								display: block;
								width: 36px;
								height: 36px;
								background-color: var(--color-base-010);
								mask-image: icon(menu);
								mask-repeat: no-repeat;
								mask-position: center center;
								mask-size: 36px;
							}
						}
					}
				}
			}
		}
	}
}

.nav-menu {
	position: fixed;
	top: 0;
	left: 0;
	bottom: 0;
	width: 264px;
	z-index: 10;
	background-color: var(--color-base-070);
	box-sizing: border-box;
	transition: all .4s;
	// z-index: 1000;

	.nav-inner {
		// display: flex;
		// flex: 1 1 100%;

		.navi-side-btm {
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;
			row-gap: 28px;
			position: absolute;
			left: 22px;
			bottom: 28px;

			.btn-menu,
			.btn-color-picker {
				display: flex;
				align-items: center;
				column-gap: 10px;
				box-sizing: border-box;
				transition: all .2s;        

				&:focus {
					outline: none;         
					border: 2px solid #000;     // 기본 outline 제거
				}

				&::before {
					content: '';
					display: block;
					cursor: pointer;
				}

				.btn-text {
					@include hidden;
				}
			}

			.btn-menu {
				&::before {
					width: 24px;
					height: 24px;
					background-color: var(--color-base-030);
					mask-image: icon(menu-open);
				}
			}

			.btn-color-picker {
				&::before {
					width: 21px;
					height: 21px;
					border-radius: 50%;
					border: 2px solid var(--color-base-100);
					background-color: var(--color-base-070);
					box-shadow: 0px 1.2px 7.2px 0px rgba(0, 0, 0, 0.31);
				}
			}
		}

		.navi-side-top {
			display: flex;
			align-items: center;
			column-gap: 10px;
			height: 80px;
			border-bottom: 1px solid var(--color-base-060);
			padding-left: 54px;



			.nav-logo {
				width: 100%;

				.logo-bx {
					display: flex;
					align-items: center;
					height: 20px;

					.logo {
						display: flex;
						align-items: center;
						height: 20px;
						width: 155px;
						background-image: url(../images/ucomp-logo.svg);
						opacity: 1;
						transition:  opacity 0.4s ease-in-out;

						.logo-tx {
							@include hidden;
							font-size: 16px;
							line-height: 24px;
						}
					}

					&::before {
						content: '';
						display: block;
						width: 24px;
						height: 24px;
						position: absolute;
						left: 24px;
						mask-image: icon(home);
						mask-repeat: no-repeat;
						mask-size: cover;
						background-color: var(--color-base-040);
						opacity: 0;
					}
				}
			}
		}
	}



	.nav-side {
		.lnb-list {
			display: flex;
			flex-direction: column;
			row-gap: 12px;
			margin-top: 12px;

			.lnb-item {
				position: relative;

				.lnb-name {
					position: relative;
					display: block;
					padding: 13px 0 13px 56px;
					box-sizing: border-box;

					.lnb-tx {
						display: block;
						opacity: 1;
						transition: all .1s;
						width: 205px;
						overflow: hidden;
						color: var(--color-base-040);
						line-height: 20px;
						font-size: 14px;
						font-weight: 500;
					}


					&::before {
						content:'';
						display: block;
						position: absolute;
						top: 11px;
						left: 24px;
						width: 24px;
						height: 24px;
						transition: all .2s;
						background-color: var(--color-base-040);						
					}
				}

				&:nth-child(1) {
					.lnb-name {&::before {mask-image: icon(mail);}}
				}
				&:nth-child(2) {
					.lnb-name {&::before {mask-image: icon(user);}}
				}
				&:nth-child(3) {
					.lnb-name {&::before {mask-image: icon(board);}}
				}
				&:nth-child(4) {
					.lnb-name {&::before {mask-image: icon(operate);}}
				}
				&:nth-child(5) {
					.lnb-name {&::before {mask-image: icon(system);}}
				}


				&:not(:first-child) {
					.lnb-name {
						.lnb-tx {
							position: relative;

							&::after {
								content: '';
								display: block;
								position: absolute;
								top: 4px;
								right: 26px;
								width: 14px;
								height: 14px;
								mask-image: icon(arrow-down);
								mask-repeat: no-repeat;
								background-color: var(--color-base-040);
								transition: all .2s;
							} 
						}
					}
				}

				&::before {
					mask-repeat: no-repeat;
					mask-position: center center;
					mask-size: 20px;
					background-color: var(--color-base-030);
				}

				&.menu-on {
					.lnb-name {
						.lnb-tx {
							position: relative;
							color: var(--color-base-010);
						}

						&::after {
							content:'';
							display: block;
							position: absolute;
							top: 0;
							left: 12px;
							bottom: 0;
							right: 12px;
							transition: all .2s;
							z-index: -1;
							border-radius: 4px;
							background-color: var(--color-base-010);
							opacity: 0.05;						
						}
					}

					&:not(:first-child) {
						.lnb-name {
							.lnb-tx {
								position: relative;
								color: var(--color-base-010);
	
								&::after {
									transform: rotate(180deg);
									transition: all .2s;
									background-color: var(--color-base-010);
								} 
							}
						}
					}

					.lnb-name {
						color: #E60A21;
						font-weight: 500;
	
						&::before {
							background-color: var(--color-base-010);
						}
					}

					.sub-list {
						max-height: 500px;
					}
				}

				&.menu-off {
					.sub-list {
						max-height: 0;
						transition: none;
					}
				}
			}
		}


		.sub-list { 
			overflow: hidden;
			transition: max-height 1s ease-in-out;

			.sub-item {
				display: flex;

				.sub-name {
					display: block;
					flex-shrink: 0;
					position: relative;
					padding: 8px 0 8px 61px;
					font-size: 14px;
					line-height: 20px;
					font-weight: 500;
					box-sizing: border-box;
					overflow: hidden;
					color: var(--color-base-010);
				}

				&.on {
					.sub-name {
						word-break: keep-all;
						font-weight: 500;
						text-decoration: underline;
					}
				}
			}
		}
	}
}

.admin-wrap {
	height: 100vh;
	padding: 8px 8px 8px 0;
	box-sizing: border-box;

	.inner-wrap {
		height: 100%;
		padding: 0 0 0 265px; 
		transition: all .4s;
		box-sizing: border-box;
	}

	&.close {
		.inner-wrap {
			padding: 0 0 0 72px;

			.nav-menu {
				width: 72px;
				background-color: var(--color-base-070);
				z-index: 10;

				.navi-side-top {
					.nav-logo {
						display: block;

						.logo-bx {
							.logo {
								background-image: none;
								opacity: 0;
								transition:  opacity 0.4s;
							}

							&::before {
								opacity: 1;
								transition:  opacity 0.4s;
							}
						}
					}

				}

				.nav-side {
					.lnb-list {
						.lnb-item {
							.lnb-name {
								.lnb-tx {
									opacity: 0;
									transition: all .2s ease-in-out;
								}
	
								&::before {
									background-color: var(--color-base-040);;
								}
							}

							&.menu-on {
								background-image: none;
	
								.lnb-name {
									// height:24px;
									background-color: transparent;

									&::after {
										content: '';
										display: block;
										position: absolute;
										width: 48px;
										height: 48px;
										border-radius: 4px;
										background-color: var(--color-base-010);
										opacity: 0.05;
									}

									&::before {background-color: var(--color-base-010); z-index: 2;}
								}
	
								.sub-list {
									display: none;
								}
							}
						}			
					}

					.sub-list {
						.sub-item {
							sub-name {
								padding-left: 36px;

								&::before {
									left: 16px;
								}
								&::after {
									left: 16px;
								}
								
							}
						}
					} 
				}

				.navi-side-btm {
					.btn-menu {
						&::before {
							mask-image: icon(menu-close);
						}
					}

				}
			}
	
			.container {
				.header {
					min-width: 200px;
				}
			}
		}


	}
} 

// 컬러 툴팁
.color-picker-layer {
	display: none;
	position: absolute;
	left: 33px;
	bottom: 44px;
	opacity: 0;

	&.active {
		display: block;
		opacity: 1;
	}

	.tooltip-area {
		display: flex;
		flex-direction: column;
		row-gap: 12px;
		padding: 16px;
		width: 139px;
		border-radius: 4px;
		box-sizing: border-box;
		background-color: var(--color-base-100);
		box-shadow: 0px 2px 12px 0px rgba(0, 0, 0, 0.15);

		.tooltip-head {
			line-height: 20px;
			font-size: 14px;
			color: var(--color-base-010);
			font-weight: 600;
			letter-spacing: -0.56px;
		}

		.tooltip-body {
			.data-list {
				display: flex;
				align-items: center;
				column-gap: 4px;

				.data-item {
					width: 24px;
					height: 24px;

					&::before {
						content: '';
						display: block;
						width: 21px;
						height: 21px;
						border-radius: 50%;
						border: 2px solid var(--color-base-100);
						background-color: var(--color-base-070);
						box-shadow: 0px 1.2px 7.2px 0px rgba(0, 0, 0, 0.31);
					}

					&.color-01 {&::before {background-color: var(--color-base-070);}}
					&.color-02 {&::before {background-color: var(--color-primary-010);}}
					&.color-03 {&::before {background-color: var(--color-secondary-010);}}
					&.color-04 {&::before {background-color: var(--color-base-000);}}

					.data-name {
						@include hidden;
					}
				}
			}
		}
	}
}


#footer {
	display: none;
	flex: 1 1 100%;
	box-shadow: inset 0px 1px 0px 0px var(--color-base-070);
	background-color: var(--color-base-090);

	.footer-inner {
		padding: 40px 20px;
	}
}

.container {
	display: flex;
	flex-direction: column;
	min-width: 1320px;
	height: 100%;
	border-radius: 12px;
	box-sizing: border-box;
	background-color: var(--color-base-100);
	border: 1px solid var(--color-base-060);
	box-shadow: 0px 1px 2px 0px rgba(0, 0, 0, 0.10), 0px 1px 3px 0px rgba(0, 0, 0, 0.05);
}

.login-header {
	position: fixed;
	top: 32px;
	left: 40px;

	.login-subject {
		.logo-name {
			&::before {
				content: '';
				display: block;
				width: 155px;
				height: 20px;
				mask-image: url(../images/ucomp-logo.svg);
				mask-repeat: no-repeat;
				mask-size: cover;
				mask-position: center;
				background-color: var(--color-primary-010);
			}

			.logo-text {
				@include hidden
			}
		}
	}
}

.login-container {
	background-color: var(--color-base-100);
}


// 회원 상세
.sms-wrap,
.pop-mem-wrap {
	position: relative;
	width: 1280px;
	height: 100%;
    margin: 0 auto;

	.pop-inner-wrap {
		height: 100%;
		background-color: var(--color-base-100);
	}

	.pop-header {
		.pop-header-util {
			position: absolute;
			top: 16px ;
			right: 24px;
			box-sizing: border-box;
			
			.btn-item {
				position: relative;

				.btn-h {
					display: flex;
					width: 24px;
					height: 24px;

					.btn-text {
						@include hidden;
					}

					&::before {
						content: '';
						position: absolute;
						top: 0;
						left: 0;
						display: block;
						width: 24px;
						height: 24px;
						mask-repeat: no-repeat;
						mask-position: center center;
						mask-size: cover;
					}

					&.search {
						position: relative;

						&::before {
							background-color: var(--color-base-010);
							mask-image: icon(usersearch);
						}
					}
				}

				.h-layer {
					top: 32px;
				}
			}
		}
	}

	.pop-container {
		padding: 24px 32px;
		background: var(--color-base-100);

		.data-display.member-info {
			padding-bottom: 24px;
			display: flex;
			align-items: flex-start;
			justify-content: space-between;
			border-bottom: 1px solid var(--color-base-060);

			.data-list {
				.data-item {
					.data-wrap {
						display: flex;
						align-items: center;
						column-gap: 12px;

						.data-head {
							display: flex;
							align-items: center;
							line-height: 40px;
							font-size: 26px;

							.data-subject {
								.data-name {
									color: var(--color-base-010);
									font-weight: 700;
								}
							}

							.data-summary {
								display: flex;
								align-items: center;
								color: var(--color-base-040);

								&::before {
									content: '/';
									display: block;
									margin: 0 0.25em;
								}
							}
						}

						.data-body {
							.mark-list {
								display: flex;
								column-gap: 6px;

								.mark-item {
									padding: 2px 8px;
									border-radius: 90px;
									box-sizing: border-box;
									line-height: 20px;
									font-size: 14px;
									font-weight: 600;
									color: var(--color-base-100);

									&.type-01 {
										background-color: #A8B6C5;
									}

									&.type-02 {
										background-color: #0E85FF;
									}

									&.type-03 {
										background-color: #00C48A;
									}
								}
							}
						}
					}
				}
			}

			.button-area {
				column-gap: 6px;
			}
		}

		.section.info-bx {
			margin-top: 24px;

			.data-display {
				.data-list {
					display: flex;
					column-gap: 24px;

					.data-item {
						.data-wrap {
							display: flex;
							flex-direction: column;
							row-gap: 12px;

							.data-head {
								display: flex;
								align-items: center;
								justify-content: space-between;

								.data-name {
									line-height: 30px;
									font-size: 20px;
									font-weight: 700;
								}

								.button-area {
									column-gap: 6px;
								}
							}
						}
					}
				}	
			}
		}
	}
}

